/// app.js
import React, { useEffect } from 'react';
import { AudaExploreDamageView } from './js/CarView';
function App() {

  let carView;
  useEffect(() => {
    carView = new AudaExploreDamageView();
    carView.objFileNames = ['iphone12.obj'];

    const name = 'RIO';
    const carFolder = process.env.PUBLIC_URL + '/models/' + name + '/Car/'
    carView.init(carFolder, () => { });

  });

  const onDownload = () => {
    carView.capture();
  }

  return (<>
    <button onClick={onDownload}> download</button>
    <div id="AudaViewport"></div>
  </>)
    ;
}

export default App;
